export default {
	props : {
		orderInfo : {
			type: Object,
			defautl : ()=>({})
		},
		message : {
			type : String,
			default : ''
		}
	},
	computed : {
		myMsg : {
			get(){
				return this.message;
			},
			set(val){
				this.$emit('update:message',val)
			}
		}
	}
}
